<template>
  <v-dialog
    v-model="dialog"
    width="500"
    persistent>
    <template v-slot:activator="{ on }">
      <v-btn 
        depressed
        v-on="on"
        class="mr-3"
        color="primary">
        <v-icon class="mr-3">mdi-plus-circle</v-icon>
        Tambah data
      </v-btn>
    </template>

    <v-card>
      <v-card-title class="py-2">
        Tambah Sekolah
      </v-card-title>
      <v-divider></v-divider>
      <v-form v-model="valid" ref="form">
        <v-card-text class="pa-6 pb-0">
          <v-text-field
            v-model="dataSekolah.nama"
            label="Nama"
            outlined
            dense
            :rules="requiredRule"
            autocomplete="off"
          ></v-text-field>
          <v-autocomplete
            v-model="dataSekolah.provinsi_id"
            :items="dataProv"
            item-text="nama"
            item-value="id"
            label="Provinsi"
            outlined
            dense
            :rules="requiredRule"
            autocomplete="off"
          ></v-autocomplete>
          <v-autocomplete
            :disabled="dataSekolah.provinsi_id===''?true:false"
            v-model="dataSekolah.kabupaten_id"
            :items="dataKab"
            item-text="nama"
            item-value="id"
            label="Kabupaten"
            outlined
            dense
            :rules="requiredRule"
            autocomplete="off"
          ></v-autocomplete>
          <v-select
            v-model="dataSekolah.jenjang_id"
            :items="dataJenjang"
            item-text="nama"
            item-value="id"
            label="Jenjang"
            outlined
            dense
            :rules="requiredRule"
            autocomplete="off"
          ></v-select>
          <v-select
            v-model="dataSekolah.kurikulum_id"
            :items="dataKurikulum"
            item-text="nama"
            item-value="id"
            label="Kurikulum"
            outlined
            dense
            :rules="requiredRule"
            autocomplete="off"
          ></v-select>
          <v-text-field
            v-model="dataSekolah.akreditasi"
            label="Akreditasi"
            outlined
            dense
            :rules="requiredRule"
            autocomplete="off"
          ></v-text-field>
        </v-card-text>

        <v-divider></v-divider>

        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn
            color="primary"
            text
            @click="dialog = false">
            kembali
          </v-btn>
          <v-btn
            :disabled="!valid"
            color="primary"
            @click.prevent="validate"
            type="submit"
            depressed>
            simpan
          </v-btn>
        </v-card-actions>
      </v-form>
    </v-card>
  </v-dialog>
</template>

<script>
  import { mapState } from 'vuex'
  import Notif from '@/components/Notif'

  export default {
    data () {
      return {
        dialog: false,
        valid: true,
        dataSekolah: {
          nama: '',
          provinsi_id: '',
          kabupaten_id: '',
          jenjang_id: '',
          kurikulum_id: '',
          akreditasi: ''
        },
        requiredRule: [v => !!v || 'Data harus diisi'],
        msg: {
          success: 'Data berhasil disimpan',
          error: 'Data gagal disimpan',
          visible: false
        }
      }
    },

    components: { Notif },

    computed: {
      ...mapState('dataSekolah', ['status', 'isLoading']),
      ...mapState('dataJenjang', ['dataJenjang']),
      ...mapState('dataKurikulum', ['dataKurikulum']),
      ...mapState('dataTempat', ['dataProv', 'dataKab'])
    },

    watch: {
      'dataSekolah.provinsi_id': function (val) {
        this.$store.dispatch('dataTempat/getDataKab', val)
      }
    },

    methods: {
      validate () {
        if (this.$refs.form.validate()) {
          console.log(this.dataSekolah)
        }
      }
    }
  }
</script>
