<template>
  <v-dialog
    v-model="dialog"
    width="500"
    persistent>
    <template v-slot:activator="{ on }">
      <v-btn 
        v-on="on"
        icon 
        color="primary">
        <v-icon small>mdi-pencil</v-icon>
      </v-btn>
    </template>

    <v-card>
      <v-card-title class="py-2">
        Edit Sekolah
      </v-card-title>
      <v-divider></v-divider>
      <v-form v-model="valid" ref="form">
        <v-card-text class="pa-6 pb-0">
          <v-text-field
            v-model="data.sekolah"
            label="Nama"
            outlined
            dense
            :rules="requiredRule"
            autocomplete="off"
          ></v-text-field>
          <v-autocomplete
            v-model="data.provinsi.id"
            :items="detailProv"
            item-text="nama"
            item-value="id"
            label="Provinsi"
            outlined
            dense
            :rules="requiredRule"
            autocomplete="off"
          ></v-autocomplete>
          <v-autocomplete
            :disabled="data.provinsi.id===''?true:false"
            v-model="data.kabupaten.id"
            :items="detailKab"
            item-text="nama"
            item-value="id"
            label="Kabupaten"
            outlined
            dense
            :rules="requiredRule"
            autocomplete="off"
          ></v-autocomplete>
          <v-select
            v-model="data.jenjang.id"
            :items="detailJenjang"
            item-text="nama"
            item-value="id"
            label="Jenjang"
            outlined
            dense
            :rules="requiredRule"
            autocomplete="off"
          ></v-select>
          <v-select
            v-model="data.kurikulum.id"
            :items="detailKurikulum"
            item-text="nama"
            item-value="id"
            label="Kurikulum"
            outlined
            dense
            :rules="requiredRule"
            autocomplete="off"
          ></v-select>
          <v-text-field
            v-model="data.akreditasi"
            label="Akreditasi"
            outlined
            dense
            :rules="requiredRule"
            autocomplete="off"
          ></v-text-field>
        </v-card-text>

        <v-divider></v-divider>

        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn
            color="primary"
            text
            @click="dialog = false">
            kembali
          </v-btn>
          <v-btn
            :disabled="!valid"
            color="primary"
            :loading="isLoading"
            @click.prevent="validate"
            type="submit"
            depressed>
            simpan
          </v-btn>
        </v-card-actions>
      </v-form>
    </v-card>
  </v-dialog>
</template>

<script>
  import { mapState } from 'vuex'
  import Notif from '@/components/Notif'

  export default {
    props: ['data'],

    data () {
      return {
        dialog: false,
        valid: true,
        requiredRule: [v => !!v || 'Data harus diisi'],
        msg: {
          success: 'Data berhasil disimpan',
          error: 'Data gagal disimpan',
          visible: false
        }
      }
    },

    components: { Notif },

    computed: {
      ...mapState('dataSekolah', ['status', 'isLoading']),
      ...mapState('dataJenjang', ['dataJenjang']),
      ...mapState('dataKurikulum', ['dataKurikulum']),
      ...mapState('dataTempat', ['dataProv', 'dataKab']),
      detailProv: {
        get() {
          return this.dataProv
        },
        set(val) {
          this.dataProv.filter(prov => prov.id === this.data.provinsi.id)
        }
      }, 
      detailKab: {
        get() {
          return this.dataKab
        },
        set(val) {
          this.dataKab.filter(kab => kab.id === this.data.kabupaten.id)
        }
      }, 
      detailJenjang: {
        get() {
          return this.dataJenjang
        },
        set(val) {
          this.dataJenjang.filter(jenjang => jenjang.id === this.data.jenjang.id)
        }
      }, 
      detailKurikulum: {
        get() {
          return this.dataKurikulum
        },
        set(val) {
          this.dataKurikulum.filter(kurikulum => kurikulum.id === this.data.kurikulum.id)
        }
      }
    },

    watch: {
      dialog(val) {
        if (val === true) {
          this.$store.dispatch('dataTempat/getDataKab', this.data.provinsi.id)
        }
      },
      'data.provinsi.id': function (val) {
        this.$store.dispatch('dataTempat/getDataKab', val)
      }
    },

    methods: {
      validate () {
        if (this.$refs.form.validate()) {
          const data = {
            nama: this.data.sekolah,
            kode_sekolah: this.data.id,
            jenjang_id: this.data.jenjang.id,
            kabupaten_id: this.data.kabupaten.id,
            provinsi_id: this.data.provinsi.id,
            akreditasi: this.data.akreditasi,
            kurikulum_id: this.data.kurikulum.id
          }
          this.$store.dispatch('dataSekolah/putDataSekolah', data)
          this.$store.dispatch('dataSekolah/updateIsLoading', true)
          this.msg.visible = true
        }
      },
      visible(val) {
        this.msg.visible = val
        this.$store.commit('dataSekolah/updateStatus', null)
      }
    }
  }
</script>
